import React, { useState, useEffect, createContext } from 'react';
import { mapUsersToMessages, mergeMessages, mergeUsers, Messaging } from '../utils/messages'
import { ajax } from '../utils/client'
import { Users } from './Users'
import { Messages } from './Messages'
import { UserContextProvider } from './UserContext'

export class Welcome extends React.Component {

  constructor() {
    super();
    this.state = {
      messages: [],
      users: []
    }
  }

  // state = []

  componentDidMount() {
    console.log('****init')

    // const users = await ajax(`/agent/users`, null, null, { updateLastActive: false })
    // console.log('users', users)
    let init

    Messaging.start(({ users, messages, unread }) => {
      // if (users.length || messages.length || unread.length) {
        console.log('data', { users, messages, unread })
        this.setState({
          ...this.state,
          users: mergeUsers(this.state.users, users, unread),
          messages: mergeMessages(this.state.messages, messages),
        })
      // }
    })
  }

  render() {
    const { users, messages } = this.state

    const usersData = mapUsersToMessages(users, messages)

    return (
      <div>
        {/*<div className="all-messages">*/}
        {/*  {messages.map(msg => <div key={msg.id}>{JSON.stringify(msg)}</div>)}*/}
        {/*</div>*/}
        <div className="welcome">
          <UserContextProvider>
            <Users usersData={usersData} />
            <Messages usersData={usersData} />
            {/*<UserMessages messages={messages} />*/}
          </UserContextProvider>
        </div>
      </div>
    );

  }

}


