import React, { useContext, Fragment } from 'react';
import { UserContext } from './UserContext'
import { ajax } from '../utils/client'
import { isIgnored, isImportant, isLocked, isLockedByAgent, AppStorage } from '../utils/utils'


const toggleLockUser = (userId, locked) => e => {
  e.stopPropagation()
  const agentId = locked ? AppStorage.agent.id : undefined
  ajax('/agent/lockUser', 'POST', { userId, agentId })
}

export function User({ user }) {

  const [userId, setUserId] = useContext(UserContext)

  const handleClick = () => {
    setUserId(user.id)
    ajax('/agent/readUserMessages', 'POST', { userId: user.id, agentId: AppStorage.agent.id })
  }

  return (
    <div className={
      `user ${user.id === userId && 'selected'} ${isImportant(user) && 'important'}`
    } onClick={handleClick}>
      <div className="user-info-descr">
        <div className="user-id">{user.id}</div>
        <div className={`user-status ${isImportant(user) && 'important'} ${isIgnored(user) && 'ignored'}`}>{user.status}</div>
        <div>{user.description}</div>
      </div>
      <div className="user-info-tokens">
        <div>Tokens: <b>{user.tokens.length}</b></div>
        {user.newMsg && <div><img src="/msg.png" height="44"/></div>}
      </div>
      <div className="user-actions">
        {/*<div>*/}
        {/*  {user.isTyping && <Typing visible={user.isTyping} />}*/}
        {/*</div>*/}
        <Lock user={user} />
      </div>
    </div>
  );
}

function Lock({ user }) {
  const { id, lockedBy } = user
  const lockedByAgent = lockedBy && AppStorage.agents.find(a => a.id === lockedBy)

  const lockedByCurrentAgent = isLockedByAgent(user)

  return (
    <div className={`user-lock ${isLocked(user) && 'locked'} ${lockedByCurrentAgent && 'lockedByMe'}`}>
      {lockedBy ? (
          <Fragment>
            <span>Locked by <b>{lockedByCurrentAgent ? 'me' : lockedByAgent.username}</b></span>
            {lockedByCurrentAgent && <button onClick={toggleLockUser(id, false)}>unlock</button>}
          </Fragment>
        )
        : <button onClick={toggleLockUser(id, true)}>lock</button>
      }
    </div>
  )
}

class Typing extends React.Component {

  state = {
    visible: true
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({ visible: false })
    }, 3000)
  }

  render() {
    const { visible } = this.state
    // const { visible } = this.props
    return <div> {visible ? 'typing...' : null} </div>
  }

}


