import { ajax } from './client'
import { ACTIONS_TO_NOTIFY, UserActions, UserStatus } from './constants'
import { config } from './../config/config'
import { notify } from './notification'
import { AppStorage } from './utils'


const MSG_PULLING_INTERVAL = config.pullingInterval

const isImportantMsg = msg => {
  return ACTIONS_TO_NOTIFY.includes(msg.action)
}

export const Messaging = {
  init: false,

  start(okCallback, errCallback) {

    setInterval(async () => {
      const data = await ajax(`/agent/data/${AppStorage.agent.id}`)

      if (this.init) {
        notify(data)
      }

      this.init = true
      okCallback(data)

    }, MSG_PULLING_INTERVAL);
  },

  async send(userId, action) {
    ajax('/message', 'PUT', { userId, action })
      .catch(e => window.alert('Error for', action.type))
  }



}


export const mergeUsers = (oldUsers, newUsers, unread) => {

  newUsers.forEach(newUser => {
    const idx = oldUsers.findIndex(o => o.id === newUser.id)

    if (idx < 0) {
      oldUsers.push(newUser)
      setTimeout(function() {
        if (newUser.status === UserStatus.NEW) {
          newUser.status = UserStatus.OLD
        }
      }, 10 * 1000)
    } else {
      oldUsers[idx] = newUser
    }
  })


  return oldUsers.reduce((acc, user) => {
    const newMsg = !!(unread.find(o => o.userId === user.id))

    return [
      ...acc,
      { ...user, newMsg }
    ]

  }, [])

}


export const filterUserMessages = (user, messages) =>
  messages.filter((msg) => msg.userId === user.id)


export const mapUserToMessages = (user, messages) => {
  const isTyping = messages.length && messages[messages.length - 1].action === UserActions.TYPING

  const extra = messages.reduce((acc, msg) => {
    if (isImportantMsg(msg)) {
      acc.tokens.push(msg.payload)
      acc.lastImportantMsgTmstp = msg.time
    }

    return acc
  }, { tokens: [], lastImportantMsgTmstp: null })


  const ret = {
    ...user,
    isTyping,
    tokens: extra.tokens,
    lastImportantMsgTmstp: extra.lastImportantMsgTmstp,
    messages
  }

  setTimeout(function() {
    ret.isTyping = false
  }, 2 * 1000)

  return ret

}

export const mapUsersToMessages = (users, messages) => {
  return users.reduce((acc, user) => {
    return {
      ...acc,
      [user.id]: mapUserToMessages(user, filterUserMessages(user, messages))
    }
  }, {})

}

const messagesAlreadyExists = (messages, msg) => {
  return messages.find(m => m.id === msg.id)
}

export const mergeMessages = (oldMessages, newMessages) => {
  return newMessages.reduce((acc, item) => {
    if (!messagesAlreadyExists(acc, item)) {
      acc.push(item)
    }

    return acc
  }, oldMessages)

}

