import React, { useState, useEffect } from 'react';
import { User } from './User'
import { UserStatus } from '../utils/constants'
import { isIgnored, isImportant, isLockedByAgent, AppStorage } from '../utils/utils'



export function Users({ usersData }) {

  const sorted = sortItems(Object.values(usersData))

  const [filter, setFilter] = useState('')
  const filtered = filterItems(sorted, filter)

  return (
    <div className="users-container ">
      <Filter filter={filter} setFilter={setFilter} />

      <div className="sidebar users ">
        {filtered.map(user => <User key={user.id} user={user} />)}
      </div>
    </div>
  );
}

function Filter({ filter, setFilter }) {

  return (
    <div className="users-filter">
      Filter <input type="text" value={filter} onChange={e => setFilter(e.target.value)} />
      <button onClick={() => setFilter('')}>clear</button>
    </div>
  )

}

const criteria = predicate => u => predicate(u) ? -1 : 1
const lockCriteria = criteria(isLockedByAgent)
const importantCriteria = criteria(isImportant)
const ignoreCriteria = criteria(isIgnored)

const sortItems = users => {
  return users.sort((u1, u2) => {

    const ign1 = ignoreCriteria(u1)
    const ign2 = ignoreCriteria(u2)

    if (ign1 !== ign2) {
      return ign2 - ign1;
    }

    const lock1 = lockCriteria(u1)
    const lock2 = lockCriteria(u2)

    if (lock1 !== lock2) {
      return lock1 - lock2
    }

    const imp1 = importantCriteria(u1)
    const imp2 = importantCriteria(u2)

    if (imp1 !== imp2) {
      return imp1 - imp2
    }


    const lastImportantMsgTmstp1 = u1.lastImportantMsgTmstp
    const lastImportantMsgTmstp2 = u2.lastImportantMsgTmstp

    if (lastImportantMsgTmstp1 && lastImportantMsgTmstp2) {
      return lastImportantMsgTmstp2 - lastImportantMsgTmstp1
    } else if (!lastImportantMsgTmstp1 && lastImportantMsgTmstp2) {
      return 1
    } else if (lastImportantMsgTmstp1 && !lastImportantMsgTmstp2) {
      return -1
    }



    /*const tokens1 = u1.tokens.length
    const tokens2 = u2.tokens.length

    if (tokens1 !== tokens2) {
      return tokens2 - tokens1
    }*/


    return u2.id - u1.id
  })
}

const filterItems = (users, filter) => {
  if (!filter || filter.trim() === '') {
    return users
  }

  return users.filter(u =>
    u.id.toString().includes(filter)
    || (u.description && u.description.includes(filter)))
}


