import React, { useState, Fragment, useEffect } from 'react';
import { AppStorage } from '../utils/utils'
import { BankTypes } from '../utils/constants'


export function Auth(props) {
  const isLogged = AppStorage.agent && loggedAgent(AppStorage.agent.username, AppStorage.agent.password)
  const [logged, setLogged] = useState(isLogged)

  return logged ? <Fragment>{props.children}</Fragment> : <Login setLogged={setLogged} />
}

function Login({ setLogged }) {

  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [bank, setBank] = useState(BankTypes.ALPHA)
  const [error, setError] = useState(false)
  
  return (
    <div className="login">
      <div>
        <select id="banks" name="banks" onChange={(e) => setBank(e.target.value)} defaultValue={BankTypes.PRO_CREDIT}>
          <option value={BankTypes.ALFA}>{BankTypes.ALPHA}</option>
          <option value={BankTypes.RAIFFEISEN}>{BankTypes.RAIFFEISEN}</option>
          <option value={BankTypes.UNI_CREDIT}>{BankTypes.UNI_CREDIT}</option>
          <option value={BankTypes.BCR}>{BankTypes.BCR}</option>
          <option value={BankTypes.PRO_CREDIT}>{BankTypes.PRO_CREDIT}</option>
        </select>
      </div>
      <div>
        <input type="text" value={username} onChange={e => setUsername(e.target.value)} placeholder="username" />
      </div>
      <div>
        <input type="password" value={password} onChange={e => setPassword(e.target.value)} placeholder="password" />
      </div>
      <div>
        {/* <button onClick={e => authenticate(username, password, setError, setLogged)}>login</button> */}
        <button onClick={e => authenticate(username, password, setError, setLogged, bank)}>login</button>
      </div>
      {error && <div className="error">Invalid Credentials</div>}
    </div>
  )

}

const authenticate = (username, password, setError, setLogged, bank) => {
  const agent = loggedAgent(username, password)
  AppStorage.bank = bank
  if (agent) {
    AppStorage.agent = agent
    setError(false)
    setLogged(true)
  } else {
    setError(true)
  }
}

const loggedAgent = (username, password) => AppStorage.agents.find(agent => agent.username === username && agent.password === password)


